.products-wrapper {
    padding-top: 98px;
    -webkit-animation: slideUp 300ms linear;
            animation: slideUp 300ms linear;
    -webkit-animation-delay: 150ms;
            animation-delay: 150ms;
  }
  @media (max-width: 991px) {
    .products-wrapper {
      padding-top: 68px;
    }
  }
  @media (max-width: 480px) {
    .products-wrapper {
      padding-top: 52px;
    }
  }
  .products {
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    padding: 16px;
    max-width: 960px;
    margin: 0 auto;
  }
  @media (max-width: 768px) {
    .products {
      padding: 8px;
    }
  }
  .products .product {
    min-height: 355px;
    -ms-flex-preferred-size: calc(25% - 32px);
        flex-basis: calc(25% - 32px);
    -webkit-flex-basis: calc(25% - 32px);
  }
  @media (max-width: 768px) {
    .products .product {
      -ms-flex-preferred-size: calc(33% - 16px);
          flex-basis: calc(33% - 16px);
      -webkit-flex-basis: calc(33% - 16px);
      margin: 8px;
    }
  }
  @media (max-width: 480px) {
    .products .product {
      -ms-flex-preferred-size: calc(50% - 16px);
          flex-basis: calc(50% - 16px);
      -webkit-flex-basis: calc(50% - 16px);
      min-height: 313px;
    }
    .products .product .product-name {
      height: 20px;
      overflow: hidden;
    }
    .products .product .product-action {
      padding: 16px 8px;
    }
  }
  @media (max-width: 320px) {
    .products .product {
      -ms-flex-preferred-size: 100%;
          flex-basis: 100%;
      -webkit-flex-basis: 100%;
    }
  }
  .fadeIn-enter {
    opacity: 0.01;
  }
  .fadeIn-enter.fadeIn-enter-active {
    opacity: 1;
    -webkit-transition: opacity 500ms ease-in;
    -o-transition: opacity 500ms ease-in;
    transition: opacity 500ms ease-in;
  }
  .fadeIn-leave {
    opacity: 1;
  }
  .fadeIn-leave.fadeIn-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    -o-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
  }
  .product:nth-child(1) {
    -webkit-transition-delay: 200ms;
         -o-transition-delay: 200ms;
            transition-delay: 200ms;
  }
  .product:nth-child(2) {
    -webkit-transition-delay: 200ms;
         -o-transition-delay: 200ms;
            transition-delay: 200ms;
  }
  .product:nth-child(3) {
    -webkit-transition-delay: 400ms;
         -o-transition-delay: 400ms;
            transition-delay: 400ms;
  }
  .product:nth-child(4) {
    -webkit-transition-delay: 600ms;
         -o-transition-delay: 600ms;
            transition-delay: 600ms;
  }
  .product:nth-child(5) {
    -webkit-transition-delay: 800ms;
         -o-transition-delay: 800ms;
            transition-delay: 800ms;
  }
  .product:nth-child(6) {
    -webkit-transition-delay: 1000ms;
         -o-transition-delay: 1000ms;
            transition-delay: 1000ms;
  }
  .product:nth-child(7) {
    -webkit-transition-delay: 1200ms;
         -o-transition-delay: 1200ms;
            transition-delay: 1200ms;
  }
  .product:nth-child(8) {
    -webkit-transition-delay: 1400ms;
         -o-transition-delay: 1400ms;
            transition-delay: 1400ms;
  }
  .product:nth-child(9) {
    -webkit-transition-delay: 1600ms;
         -o-transition-delay: 1600ms;
            transition-delay: 1600ms;
  }
  .product:nth-child(10) {
    -webkit-transition-delay: 1800ms;
         -o-transition-delay: 1800ms;
            transition-delay: 1800ms;
  }
  .product:nth-child(11) {
    -webkit-transition-delay: 2000ms;
         -o-transition-delay: 2000ms;
            transition-delay: 2000ms;
  }
  .product:nth-child(12) {
    -webkit-transition-delay: 2200ms;
         -o-transition-delay: 2200ms;
            transition-delay: 2200ms;
  }
  .product.loading {
    -webkit-animation-duration: 600ms;
            animation-duration: 600ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: contentLoader;
            animation-name: contentLoader;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    background: #f6f7f8;
    background: -webkit-gradient(linear, left top, right top, color-stop(8%, #eee), color-stop(18%, #ddd), color-stop(33%, #eee));
    background: -webkit-linear-gradient(left, #eee 8%, #ddd 18%, #eee 33%);
    background: -o-linear-gradient(left, #eee 8%, #ddd 18%, #eee 33%);
    background: linear-gradient(to right, #eee 8%, #ddd 18%, #eee 33%);
    background-size: 1000px 104px;
    height: 200px;
    position: relative;
    overflow: hidden;
  }
  .product.loading .product-image {
    height: 168px;
    width: 168px;
    margin: 16px;
    background: rgba(255, 255, 255, 0.25);
  }
  @media (max-width: 768px) {
    .product.loading .product-image {
      width: auto;
    }
  }
  .product.loading .product-text, .product.loading .product-button {
    margin: 12px 16px;
    height: 16px;
    background: rgba(255, 255, 255, 0.25);
  }
  .product.loading .product-button {
    position: absolute;
    bottom: 8px;
    width: 168px;
    height: 36px;
  }
  @media (max-width: 768px) {
    .product.loading .product-button {
      width: auto;
    }
  }
  @-webkit-keyframes contentLoader {
    0% {
      background-position: -420px 0;
    }
    100% {
      background-position: 420px 0;
    }
  }
  @keyframes contentLoader {
    0% {
      background-position: -420px 0;
    }
    100% {
      background-position: 420px 0;
    }
  }
  .no-results {
    text-align: center;
    max-width: 520px;
    margin: 0 auto;
  }
  .no-results img {
    width: 320px;
    margin: 16px 0 32px 0;
    opacity: 0.33;
  }
  .no-results h2 {
    margin-bottom: 16px;
    color: #666;
  }
  .no-results p {
    color: #999;
  }
  


